import React, { useState, useEffect } from "react";
import logo from './img/logo.svg'
import axios from 'axios'
const Admin = () => {
    const [email, setEmail] =useState('')
    const [password, setPassword] =useState('')
const proxy = process.env.REACT_APP_PROXY
    const back = async () =>{
     window.location.href="/" 
    }
    const login = async () =>{
        if(email=='')
        {
            alert('input email');
            return;
        }
        if(password=='')
        {
            alert('input password')
            return;
        }

        axios.post(proxy + '/login',{email:email,password:password}).then((res)=>{
          console.log(res);
            if(res.data=='success')
            {
                localStorage.setItem('user','administrator')
                window.location.href="administrator"
            }
            else if(res.data=='wrong_pass')
            {
                alert('wrong password')
            }
            else if(res.data=='no_exist')
            {
                alert('wrong Email')
            }
        })
    }
  return (
    <div className='bg-landing container-fluid'>
      <div className='top-header p-4'>
        <div className='row align-items-center'>
          <div className='col-md-6'>
            <div className='logo'>
              <a href="/"><img alt='SETIMAGE' src={logo} className='img-fluid' /></a>
            </div>
          </div>
          <div className='col-md-6'>
            <div className='wallet text-right'>
                <button
                  className='wallet-button rounded-pill btn'
                  onClick={e=>back()}
                >
                  Back
                </button>
            </div>
          </div>
        </div>
      </div>
      <div className="container">
          <center>
            <div className="col-lg-5 card">
                <center style={{fontWeight:'600',fontFamily:'robot',marginTop:'20px'}}>Administrator Login Info</center>
                <p style={{marginTop:'15px'}}>Email</p><input className="form-control" 
                    placeholder="Input Email"
                      value={email}
                      onChange={e => setEmail(e.target.value)}/>
                <p style={{marginTop:'15px'}}>Password</p><input className="form-control" type="password"
                    placeholder="Input Password"
                      value={password}
                      onChange={e => setPassword(e.target.value)} />
                <div className="row" style={{marginTop:'15px',marginBottom:'20px'}}>
                    <div className="col-lg-6">
                    </div>
                    <div className="col-lg-6">
                        <button className='wallet-button rounded-pill btn' style={{width:'150px'}} onClick={e=>login()}>
                            Login
                        </button>
                    </div>
                </div>
               
            </div>
        
          </center>
      </div>
    </div>
  )
};

export default Admin;




