import React, { useState, useEffect } from "react";
import logo from './img/logo.svg'
import axios from 'axios'
import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';
import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import TabPanel from '@mui/lab/TabPanel';
import DataTable, { createTheme } from "react-data-table-component";
import SortIcon from "@material-ui/icons/ArrowDownward";
import { FileUploader } from "react-drag-drop-files";

createTheme("solarized", {
    text: {
        primary: "white",
        secondary: "white",
    },
    background: {
        default: "rgba(46, 44, 44, 0.682)",
    },
    context: {
        text: "black",
    },
    sortFocus: {
        default: "white",
    },
});
const Home = () => {
  const [value, setValue] = React.useState('1');
  const [adsList, setAdsList] = useState([]);
  const [beforePass, setBeforePass] =useState('')
  const [password, setPassword] =useState('')
  const [rePassword, setRePassword] =useState('')
  const fileTypes = ["JPG", "PNG", "GIF","BMP"];
  const [file, setFile] = useState(null);
  const proxy = process.env.REACT_APP_PROXY
    const change_list = async (event, newValue) => {
    await get_ads_list()
    setValue(newValue);
    };
    const columns = [
        {
          name: "Id",
          selector: "id",
          sortable: true,
          width: '*'
        },
        {
          name: "ADS Name",
          selector: "name",
          sortable: true,
          width: '*'
        },
        {
          name: "Action",
          selector: "status",
          cell: (row) => {
            return row.status == "Active" ? <select className="form-control" style={{width:'60%'}} onChange={e=>change_status(row.id,e.target.value)}><option value="Active">Active</option><option value="DeActive">DeActive</option><option value="Delete">Delete</option></select> : <select className="form-control" onChange={e=>change_status(row.id,e.target.value)} style={{width:'60%'}}><option value="DeActive">DeActive</option><option value="Active">Active</option><option value="Delete">Delete</option></select>
          },
          sortable: true,
          width: '*'
        }
      ];
    const change = async () =>{
        if(beforePass=='')
        {
            alert('input Old password');
            return;
        }
        if(password=='')
        {
            alert('input password')
            return;
        }
        if(rePassword=='')
        {
            alert('input Confirm Password')
            return;
        }
        if(password!=rePassword)
        {
            alert('Confirm Password')
            return;
        }
        axios.post(proxy+'/change_password',{old:beforePass,password:password}).then((res)=>{
        if(res.data=='success')
            {
                alert('success')
            }
            else if(res.data=='wrong')
            {
                alert("Old password is wrong")
            }
        })
    }
    const upload = async () =>{
        if (file) {
            var data = new FormData()
            data.append('file', file)
            fetch(proxy+'/upload', {
                method: 'POST',
                body: data
            }).then((res)=>{
                console.log(res)
                if(res.statusText=='OK')
                {
                    alert('success')
                }
            })
        }
        
    }
    const get_ads_list = async () => {
        axios.post(proxy+'/get_list',{}).then((res)=>{
        if(res.data)
            {
                setAdsList(res.data)
            }
        })
    }
    const change_status = async (id,status)=>{
        console.log(id,status)
        axios.post(proxy+'/change_status',{id:id,status:status}).then((res)=>{
        if(status=='Delete')
        {
           window.location.href=""
        }    
        })
    }
    const logout = ()=>{
        localStorage.clear();
        window.location.href="/admin"
    }
    useEffect(()=>{
        if(localStorage.getItem('user')!='administrator')
        {
            window.location.href='/admin'
        }
        get_ads_list()
    },[])
  return (
    <div className='bg-landing container-fluid'>
      <div className='top-header p-4'>
        <div className='row align-items-center'>
          <div className='col-md-6'>
            <div className='logo'>
              <a href=""><img alt='SETIMAGE' src={logo} className='img-fluid' /></a>
            </div>
          </div>
          <div className='col-md-6'>
            <div className='wallet text-right'>
                <button
                  className='wallet-button rounded-pill btn' onClick={e=>logout()}
                >
                  Log  Out
                </button>
            </div>
          </div>
        </div>
      </div>
      <div className="container">
          <center>
      <Box sx={{ width: '100%', typography: 'body1' }}>
      <TabContext value={value}>
        <Box sx={{ borderBottom: 1, borderColor: 'divider',backgroundColor:'white' }}>
          <TabList onChange={change_list} aria-label="lab API tabs example">
            <Tab label="ADS management" value="1" />
            <Tab label="ADS List" value="2" />
            <Tab label="Change password" value="3" />
          </TabList>
        </Box>
        <TabPanel value="1">
        <div>
        <center>
            <div className="col-lg-5 card">
                <p></p>
                    <FileUploader handleChange={(file)=>setFile(file)} name="file" types={fileTypes} style={{color:'white'}}/>
                <div className="row" style={{marginTop:'15px',marginBottom:'20px'}}>
                <div className="col-lg-6">
                    </div>
                    <div className="col-lg-6">
                        <button className='wallet-button rounded-pill btn' style={{width:'150px'}} onClick={e=>upload()}>
                            Upload
                        </button>
                    </div>
                </div>

        </div>
        </center>
        </div>
        </TabPanel>
        <TabPanel value="2">
        <div className="container">
            <DataTable
                columns={columns}
                data={adsList}
                defaultSortField="title"
                sortIcon={<SortIcon />}
                pagination
                striped
                theme="solarized"

              />
        </div>
        </TabPanel>
        <TabPanel value="3">
        <div className="container">
          <center>
            <div className="col-lg-5 card">
                <center style={{fontWeight:'600',fontFamily:'robot',marginTop:'20px'}}>Change Password</center>
                <p style={{marginTop:'15px'}}>Old password</p><input className="form-control" type="password"
                    placeholder="Input Old passwordd"
                      value={beforePass}
                      onChange={e => setBeforePass(e.target.value)}/>
                <p style={{marginTop:'15px'}}>Password</p><input className="form-control" type="password"
                    placeholder="Input Password"
                      value={password}
                      onChange={e => setPassword(e.target.value)} />
                <p style={{marginTop:'15px'}}>Confirm Password</p><input className="form-control" type="password"
                    placeholder="Input Confirm Password"
                      value={rePassword}
                      onChange={e => setRePassword(e.target.value)} />
                <div className="row" style={{marginTop:'15px',marginBottom:'20px'}}>
                    <div className="col-lg-6">
                    </div>
                    <div className="col-lg-6">
                        <button className='wallet-button rounded-pill btn' style={{width:'150px'}} onClick={e=>change()}>
                            Change
                        </button>
                    </div>
                </div>
            </div>
          </center>
          </div>
          </TabPanel>
      </TabContext>
    </Box>
    </center>
      </div>
    </div>
  )
};

export default Home;




