import { widget } from "./charting_library"
import { useEffect, useState } from 'react';
import axios from 'axios';

export default function TradingViewComponent(props) {
const {address} = props
  const [timeframe, setTimeframe] = useState(15);
  const apiKey = 'FNQ5D1XG3IMZ55SU8NR1W9KE8AMC8IYPAC'
  const configurationData = {
    supported_resolutions: ['1', '3', '5', '15', '30', '1h', '2h', '4h', '1D', '1W'],
    exchanges: [{
      value: 'value',
      name: 'name',
      desc: 'desc',
    }
    ],
  };

  const [chartData, setChartData] = useState([]);
  const [tokenName, setTokenName] = useState('')
  const [tokenAMM, setTokenAMM] = useState('')
  const [tokenSymbol, setTokenSymbol] = useState('')
  const [tokenDescription, setTokenDescription] = useState('')
  useEffect(() => {
    async function fetchData() {
      let get_token = await axios.get(`https://api.dex.guru/v2/tokens/search/${address}?network=bsc`);
      console.log(get_token,'tokenadfasdfsdfsdf')
      if(get_token.data.data.length>0)
      {
        setTokenName(get_token.data.data[0].name)
        setTokenSymbol(get_token.data.data[0].symbol)
        setTokenDescription(get_token.data.data[0].description)
        setTokenAMM(get_token.data.data[0].AMM)
      }
      const from = '1644872497'
      const to = (new Date().getTime() / 1000).toFixed(0)
      const api = `https://api.dex.guru/v1/tradingview/history?symbol=${address}-bsc_USD&resolution=60&from=${from}&to=${to}&countback=320&apikey=${apiKey}`;
      await axios.get(api)
      .then(res => {
        console.log(res)
          if (res.data) {
            let Data = [];
            for (let index = 0; index < res.data.t.length; index++) {
              Data.push({
                time: res.data.t[index],
                low: res.data.l[index],
                high: res.data.h[index],
                open: res.data.o[index],
                close: res.data.c[index]
              })
            }
            setChartData(Data);
          }
        })
        .catch(err => {
          console.log(err, 'error');
        })
    }
    if (address) {
      fetchData();
    }
  }, [address])

  let Datafeed = {
    onReady: (callback) => {
      setTimeout(() => callback(configurationData));
    },
    resolveSymbol: async (
      symbolName,
      onSymbolResolvedCallback,
      onResolveErrorCallback,
    ) => {
      let get_token = await axios.get(`https://api.dex.guru/v2/tokens/search/${address}?network=bsc`);
      console.log(get_token,'tokenadfasdfsdfsdf')
      if(get_token.data.data.length>0)
      {
        setTokenName(get_token.data.data[0].name)
        setTokenSymbol(get_token.data.data[0].symbol)
        setTokenDescription(get_token.data.data[0].description)
        setTokenAMM(get_token.data.data[0].AMM)
      }
      var symbolData = {
        ticker: `USD`,
        name: '',
        description: '',
        type: "crypto",
        session: '24x7',
        timezone: 'Etc/UTC',
        exchange: '',
        minmov: 1,
        pricescale: 1000000000,
        has_intraday: true,
        has_no_volume: true,
        has_weekly_and_monthly: false,
        supported_resolutions: configurationData.supported_resolutions,
        volume_precision: 2,
        data_status: 'streaming',
      }
      onSymbolResolvedCallback(symbolData);
    },
    getBars: async (symbolInfo, resolution, periodParams, onHistoryCallback, onErrorCallback) => {
      const { from, to } = periodParams;
      try {

        let bars = [];
        chartData.forEach(bar => {
          if (bar.time * 1000 >= from * 1000 && bar.time * 1000 < to * 1000) {
            bars = [...bars, {
              time: bar.time * 1000,
              low: bar.low,
              high: bar.high,
              open: bar.open,
              close: bar.close,
            }];
          }
        });

        onHistoryCallback(bars, {
          noData: false,
        });

      } catch (error) {
        onErrorCallback(error);
      }
    },
  }

  useEffect(async() => {
    if (chartData.length) {
      console.log(chartData)
      window.tvWidget = await new widget({
        symbol: tokenSymbol, // default symbol
        interval: timeframe, // default interval
        fullscreen: false, // displays the chart in the fullscreen mode
        container: 'tv_chart_container',
        datafeed: Datafeed,
        library_path: './charting_library/',
        theme: 'Dark'
      });
    }
  }, [chartData])

  return (
    <div id="tv_chart_container" className='tradingChart' style={{ width: '100%', height: '100%' }}></div>
  );
}